import { Option, Select } from 'element-ui';
import Vue from 'vue';
import panZoom from 'vue-panzoom';

import App from './App.vue';

Vue.use(panZoom);
Vue.component(Select.name, Select);
Vue.component(Option.name, Option);
Vue.config.productionTip = false;

new Vue({
  render: (h) => h(App),
}).$mount("#app");
