<template>
  <div>
    <SvgPanZoom
      :key="ratingField"
      class="map-container"
      :fit="true"
      @svgpanzoom="registerSvgPanZoom"
      @onZoom="clearOver"
    >
      <svg
        width="100%"
        xmlns="http://www.w3.org/2000/svg"
        viewBox="-20 0 1234.449 760.6203"
      >
        <path
          v-for="(reg, i) in coloredRegions"
          :key="reg.title + i"
          :title="reg.title"
          :d="reg.d"
          :transform="reg.transform"
          :style="{ fill: reg.fill }"
          :class="{ hover: reg.title === overReg, disable: reg.disable }"
          :id="`path-${i}`"
          @mouseover="onOver(reg, i)"
          @mouseout="onOver(null, i)"
          @mouseleave="onOver(null, i)"
          @touchend="onOver(reg, i)"
          @click="onClick(reg, i)"
          v-click-outside="onClick"
          ref="path"
        >
          <div>
            {{ reg["Место в рейтинге"] }}
          </div>
        </path>
      </svg>
    </SvgPanZoom>
    <div
      class="map-rating"
      :style="{
        background: `linear-gradient(90deg, ${colorArr[0]} 0%, ${
          colorArr[colorArr.length - 1]
        } 100%)`,
      }"
    >
      <div class="rating-container">
        <div class="rating-title">{{ ratingField }}</div>
        <div class="rating-start">1</div>
        <div class="rating-end">{{ maxRating }}</div>
      </div>
    </div>
    <div
      class="map-info"
      :key="selectedRegion.title"
      @click="clearOver"
      v-if="selectedRegion"
    >
      <h3>
        {{ selectedRegion.title }}
        <br />
        <div class="rating-subtitle" v-if="selectedRegion[ratingField]">
          {{ ratingField }}
          <strong>
            {{ selectedRegion[ratingField] || "-" }}
          </strong>
        </div>
      </h3>
      <div>
        <div>Задач поставлено: {{ selectedRegion["Задач поставлено"] }}</div>
        <div>Задач отобрано: {{ selectedRegion["Задач отобрано"] }}</div>
        <div>Задач решено: {{ selectedRegion["Задач решено"] }}</div>

        <div>Задач внедрено: {{ selectedRegion["Задач внедрено"] }}</div>
        <div v-if="selectedRegion['Цифровой наставник']">
          Цифровой наставник: по
          {{ selectedRegion["Цифровой наставник"] }}
          {{
            declOfNum(selectedRegion["Цифровой наставник"], [
              "задаче",
              "задачам",
              "задачам",
            ])
          }}
        </div>
        <div v-if="selectedRegion['Разместил цифровую практику на СМАРТЕКЕ']">
          Разместил цифровую практику на СМАРТЕКЕ:
          {{ selectedRegion["Разместил цифровую практику на СМАРТЕКЕ"] }}
        </div>
        <div v-if="selectedRegion['Data-хакатоны']">
          Data-хакатоны:
          {{ selectedRegion["Data-хакатоны"] }}
          {{
            declOfNum(selectedRegion["Data-хакатоны"], [
              "участник",
              "участника",
              "участников",
            ])
          }}
        </div>
      </div>
    </div>
    <el-select
      class="rating-select"
      v-model="selectedRating"
      placeholder="Выберите рейтинг"
    >
      <el-option
        v-for="item in ratingSorts"
        :key="item.field"
        :label="item.field"
        :value="item.field"
      >
      </el-option>
    </el-select>
  </div>
</template>

<script>
import Gradient from "javascript-color-gradient";
import SvgPanZoom from "vue-svg-pan-zoom";
import { geoData } from "../data";
import { regionsData } from "../regions-data";
import ClickOutside from "vue-click-outside";
const colorGradient = new Gradient();
export default {
  components: { SvgPanZoom },
  directives: { ClickOutside },
  data() {
    regionsData.forEach((e) => {
      const finded = geoData.find((r) => r.title === e["Субъект РФ"]);
      if (!finded) {
        console.log(e["Субъект РФ"]);
      } else {
        Object.assign(finded, e);
      }
    });
    const regionsObj = regionsData.reduce((sum, curr) => {
      sum[curr["Субъект РФ"]] = curr;
      curr.title = curr["Субъект РФ"];
      return sum;
    }, {});
    const regions = geoData.filter((e) => !!regionsObj[e.title]);
    geoData.forEach((e) => {
      Object.assign(e, regionsObj[e.title]);
    });

    return {
      colorGradient,
      svgpanzoom: null,
      regionsObj,
      regions,
      overReg: null,
      selectedRating: "Место в рейтинге",
      ratingSort: "asc",
      ratingField: "Место в рейтинге",
      ratingSorts: [
        {
          field: "Место в рейтинге",
          sort: "asc",
        },
        {
          field: "Рейтинг регионов по задачам",
          sort: "desc",
        },
        {
          field: "Ретинг по развитию сообщества",
          sort: "desc",
        },
        {
          field: "Рейтинг по внедрению",
          sort: "desc",
        },
      ],
    };
  },
  watch: {
    selectedRating(val) {
      const rating = this.ratingSorts.find((e) => e.field === val);
      this.ratingSort = rating.sort;
      this.ratingField = rating.field;
    },
  },
  computed: {
    maxRating() {
      return this.regions.reduce(
        (sum, curr) =>
          +curr[this.ratingField] > sum ? +curr[this.ratingField] : sum,
        0
      );
    },
    colorArr() {
      colorGradient.setGradient(
        this.ratingSort === "asc" ? "#505aea" : "#dedede",
        this.ratingSort === "asc" ? "#dedede" : "#505aea"
      );
      colorGradient.setMidpoint(this.maxRating);
      return colorGradient.getArray();
    },
    selectedRegion() {
      return this.regionsObj[this.overReg];
    },
    sortedRegions() {
      return [...this.regions].sort((r1, r2) => {
        const rt1 = r1[this.ratingField] || Infinity;
        const rt2 = r2[this.ratingField] || Infinity;
        return rt1 - rt2;
      });
    },
    coloredRegions() {
      return this.sortedRegions.map((r) => {
        let fill = undefined;
        let disable = false;
        if (r[this.ratingField]) {
          fill = this.colorArr[r[this.ratingField] - 1];
        } else {
          fill = this.colorArr[this.maxRating];
        }
        return {
          ...r,
          fill,
          disable,
        };
      });
    },
  },
  methods: {
    onPan(e) {
      console.log(e);
    },
    registerSvgPanZoom(svgpanzoom) {
      this.svgpanzoom = svgpanzoom;
    },
    center() {
      if (!this.svgpanzoom) return;

      this.svgpanzoom.center();
    },
    clearOver() {
      this.overReg = null;
    },
    onClick(reg) {
      if (reg) {
        if (this.overReg === reg.title) {
          this.overReg = null;
        } else {
          this.overReg = reg.title;
        }
      }
    },
    onOver(reg, i) {
      if (reg) {
        this.overReg = reg.title;
      } else {
        this.overReg = null;
      }
    },
    declOfNum(number, titles) {
      const cases = [2, 0, 1, 1, 1, 2];
      return titles[
        number % 100 > 4 && number % 100 < 20
          ? 2
          : cases[number % 10 < 5 ? number % 10 : 5]
      ];
    },
  },
};
</script>

<style lang="scss" scoped>
* {
  outline: none;
}
.map-info {
  position: absolute;
  left: 20px;
  top: 20px;
  z-index: 2;
  padding: 25px;
  color: #fff;
  background: rgba(#505aea, 0.8);
  max-width: 100%;
  font-size: 16px;
  font-weight: 300;
  pointer-events: none;
  line-height: 1.5;
  border-radius: 15px;
  h3 {
    margin: 0;
    padding: 0;
    margin-bottom: 15px;
  }
}
.rating-subtitle {
  margin-top: -2px;
  font-size: 14px;
  font-weight: 200;
  color: rgba(#fff, 0.8);
}
.map-container {
  width: 100%;
  height: calc(100vh);
  overflow: hidden;
  position: relative;
  margin: 0 auto;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
}
.rating-select {
  position: absolute;
  right: 20px;
  top: 20px;
  width: 275px;
  max-width: calc(100% - 40px);
}
.map-rating {
  position: absolute;
  left: 50%;
  bottom: 50px;
  max-width: 300px;
  width: 100%;
  height: 10px;
  background: linear-gradient(
    90deg,
    rgba(36, 12, 0, 1) 0%,
    rgba(31, 255, 0, 1) 100%
  );
  transform: translate3d(-50%, 0, 0);
  pointer-events: none;
}
.rating-container {
  position: relative;
  width: 100%;
  height: 100%;
}
.rating-title {
  position: absolute;
  left: 50%;
  top: -24px;
  transform: translate3d(-50%, 0, 0);
  font-size: 14px;
  width: 100%;
  text-align: center;
}
.rating-end {
  position: absolute;
  right: 0%;
  top: -24px;
  transform: translate3d(50%, 0, 0);
  font-size: 14px;
}
.rating-start {
  position: absolute;
  left: 0%;
  top: -24px;
  transform: translate3d(-50%, 0, 0);
  font-size: 14px;
}
svg {
  shape-rendering: geometricPrecision;
  max-height: 100%;
  height: 100%;
  width: 100%;
  outline: none;
}
path {
  transition: 0.2s;
  fill: #efefef;
  stroke: #fff;
  &.hover {
    fill: #32dcac !important;
    cursor: pointer;
  }
  &:focus {
    outline: none;
    box-shadow: none;
  }
  &.disable {
    pointer-events: none;
  }
}
</style>
